@import './../../../src/styles/variables.scss';
@import './../../../src/styles/fonts.scss';
@import './../../../src/styles/CSStransition.scss';


.filter {
  position: relative;
  width: max-content;
  display: flex;
  justify-content: end;
  align-items: center;

  &--phone {
    width: 100%;
    height: 54px;
    border-radius: $rounding;
    background: #F7F9FB;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__sort {
    display: block;
    width: max-content;
    user-select: none;
  }

  &__select {
    border: none;
    padding-right: 24px;
    background: url("./../../images/iconss/chevron-down.svg") center right no-repeat transparent;
    cursor: pointer;

    &--active {
      background: url("./../../images/iconss/chevron-up.svg") center right no-repeat transparent;
    }
  }

  &__list {
    position: absolute;
    right: 0;
    top: 25px;
    z-index: 5;
    background-color: #fff;
    width: 166px;
    padding: 8px 0;
    border-radius: $rounding;
    box-shadow: 24px 46px 21px rgba(117, 117, 117, 0.02), 14px 26px 18px rgba(117, 117, 117, 0.08), 6px 12px 13px rgba(117, 117, 117, 0.13), 2px 3px 7px rgba(117, 117, 117, 0.15), 0px 0px 0px rgba(117, 117, 117, 0.15);

    &--phone {
      left: 0;
      top: 57px;
      width: 100%;
      height: 120px;
      overflow-y: scroll;

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  &__option {
    display: block;
    background-color: transparent;
    border: none;
    width: 100%;
    text-align: start;
    padding: 8px 24px;
    cursor: pointer;
    transition: $transition;

    &--phone {
      padding: 8px 0;
      text-align: center;
    }

    &:hover {
      background: #F1F3F5;
    }
  }
}
